"use client";

import { ReactNode, useEffect } from "react";

interface PageAnimRootProps {
  children: ReactNode;
}

export default function PageAnimRoot({ children }: PageAnimRootProps) {
  useEffect(() => {
    const pageRoot = document.getElementById("__page_root");
    if (pageRoot) {
      // Trigger the animation on mount using requestAnimationFrame
      requestAnimationFrame(() => {
        pageRoot.classList.add("page-anim--enter");
      });
    }
  }, []);

  return (
    <div id="__page_root" className="page-anim" data-loaded="false">
      {children}
    </div>
  );
}
